<template>
  <div class="main" style="max-width: 800px">
    <md-field>
      <label>이벤트 제목</label>
      <md-input v-model="submit.name"></md-input>
    </md-field>
    <md-field>
      <label>신청자 이메일</label>
      <md-input v-model="submit.email"></md-input>
    </md-field>
    <md-field>
      <label>신청자 연락처</label>
      <md-input v-model="submit.contact"></md-input>
    </md-field>
    <md-field>
      <label>신청자 홈페이지 주소</label>
      <md-input v-model="submit.homepage"></md-input>
    </md-field>
    <md-field>
      <label>주최기관</label>
      <md-input v-model="submit.host"></md-input>
    </md-field>
    <md-field>
      <input v-on:change="fileSelect()" type="file" ref="img_path" />
      <label style="margin-left : 12%"></label>
    </md-field>
    <md-field>
      <label>설명글</label>
      <md-textarea v-model="submit.text"></md-textarea>
    </md-field>
    <md-dialog-actions>
      <md-button class="md-primary" v-on:click="Register">Submit</md-button>
    </md-dialog-actions>
    <!-- ============================================== -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      
      img_path: "",
      submit:{
        name : "",
        email: "",
        contact : "",
        text :  "",
        homepage : "",
        host : "",
      }
    };
  },

  methods: {
    fileSelect(){
      console.log(this.$refs);
      this.img_path = this.$refs.img_path.files[0];
      console.log(this.img_path)
    },
      Register(){
        var data = new FormData();
        data.append('file',this.img_path)
        data.append('email',this.submit.email)
        this.$http
              .post("/supporter/assign",this.submit
              )
              .then(res => {
                  this.$http
              .post("/images/supporter/upload", data,{
                headers : {'Content-type' : 'multipart/form-data'
                }
              })
              .then(res => {
              alert("등록이 완료되었습니다.");
              this.$router.push({ name: 'home' });
              })
              .catch(err => {
              console.log("Submit failed");
              });
              })
              .catch(err => {
              console.log("Submit failed");
              });
       
        this.view_info = true;
      }

  }
};
</script>

<style lang="css">
 @media  (min-width: 975px){
   .main {
    justify-content: center;
    align-items: center; 
    margin-top:5%; 
    color : "#FFF";
    margin-left : 20%;
  }
 }
  @media  (max-width: 975px) {
    .main {
      justify-content: center;
      align-items: center; 
      margin-top:5%; 
      
          }
    }
 .md-dialog {
    max-width: 768px;
    min-height: 750px;
  }
   .demo-option {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
   }

    .select {
      max-width: 100%;
    
      margin: 15px;
      
     
    }
   .md-button{
      margin-left: 55%;
   }
  
</style>



